import React from "react";
import {
  Grid,
  Paper,
  TextField,
  Button,
  Typography,
  Link,
} from "@material-ui/core";
import FormControlLabel from "@material-ui/core/FormControlLabel";
import Checkbox from "@material-ui/core/Checkbox";
const Login = () => {
  const paperStyle = {
    padding: 20,
    height: "70vh",
    width: 400,
    margin: "40px auto",
  };
  const inputfields = { padding: "0 0 10px 0" };
  const btnstyle = {
    margin: "10px 0",
    backgroundColor: "#008bdf",
    color: "#fff",
  };
  const links = { padding: "5px 0" };
  return (
    <Grid>
      <Paper elevation={20} style={paperStyle}>
        <Grid align="center">
          <img
            src="https://smoothhiring.com/wp-content/uploads/2021/03/Smooth-hiring-logo-001-02.png"
            width="260"
            height="50"
            alt="logo"
          />
        </Grid>
        <h1> Login</h1>

        <TextField
          label="Email"
          placeholder="Enter you email"
          fullWidth
          required
          style={inputfields}
        />
        <TextField
          label="Password"
          placeholder="Enter your password"
          type="password"
          fullWidth
          required
        />
        <FormControlLabel
          control={<Checkbox name="checkBox" color="primary" />}
          label="Remember me"
        />
        <Button
          type="submit"
          variant="contained"
          style={btnstyle}
          fullWidth
          onClick={() => alert("Welcome to SmoothHiring")}
        >
          Login
        </Button>
        <Typography style={links}>
          <Link href="#">Forgot password ?</Link>
        </Typography>
        <Typography style={links}>
          Do you have an account ?<Link href="#">Sign Up</Link>
        </Typography>
      </Paper>
    </Grid>
  );
};

export default Login;
